/**
 * Error types for withdrawal processing
 */
export enum ErrorType {
  TEMPORARY = 'TEMPORARY',    // C th retry (session, network, device busy)
  PERMANENT = 'PERMANENT',    // Khng nn retry (invalid data, insufficient funds)
  UNKNOWN = 'UNKNOWN',
  FORCE_LOGIN = 'FORCE_LOGIN', // Force login again,
  FORCE_KILL_APP = 'FORCE_KILL_APP'
}

/**
 * Custom error for withdrawal processing with error type classification
 */
export class WithdrawalProcessingError extends Error {
  constructor(
    message: string,
    public readonly errorType: ErrorType,
    public readonly withdrawalId: string
  ) {
    super(message);
    this.name = 'WithdrawalProcessingError';
  }
}

/**
 * Helper function to classify error type from reason string
 */
export function classifyError(reason: string, error?: Error): ErrorType {
  const reasonLower = reason.toLowerCase();
  const errorMessage = error?.message?.toLowerCase() || '';
  
  // Li tm thi - c th retry
  const temporaryPatterns = [
    'session',
    'network',
    'timeout',
    'device',
    'locked',
    'busy',
    'connection',
    'temporary',
    'not active',
    'unexpectedly locked'
  ];
  
  // Li vnh vin - khng nn retry
  const permanentPatterns = [
    'insufficient',
    'invalid',
    'not found',
    'unauthorized',
    'forbidden',
    'bad request',
    'required'
  ];
  
  const fullText = `${reasonLower} ${errorMessage}`;
  
  if (permanentPatterns.some(pattern => fullText.includes(pattern))) {
    return ErrorType.PERMANENT;
  }
  
  if (temporaryPatterns.some(pattern => fullText.includes(pattern))) {
    return ErrorType.TEMPORARY;
  }
  
  return ErrorType.UNKNOWN;
}

